@import url('https://fonts.googleapis.com/css2?family=Poiret+One&display=swap');


:root
{
    --yellow-red-gradient: linear-gradient(to right, #ffc508, #ff1808) 30;
}

*
{   
    font-family: 'Poiret One', cursive;
}

body 
{
    background-image: url("/public/images/vectorpaint.svg");
    background-repeat: no-repeat;
    background-size: cover;
    margin: 0 auto;
    height: 100%;
}

button
{
    background: none;
    border: none;
}

.underline-gradient
{
    border-image: var(--yellow-red-gradient);
    border-bottom-width: 4px;
    border-bottom-style: solid;
}

.black
{
    background-color: rgb(129, 129, 129);
}

.red
{
    background-color: brown;
}

.letter-red
{
    color: brown;
}

.letter-yellow
{
    color: #FFBB08
}

.list-header
{
    margin: 0 auto;
}

.taxi-logo{
    transition: .2s;
}

.taxi-logo:hover{
    transform: scale(1.1);
}

.dropdown-button:hover
{
    overflow: hidden;
    transition: .5s;
} 

.gradient-text
{
    background-image: linear-gradient(90deg, #FAE22F, #FF130E);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.without-marker
{
    list-style-type: none;
}

polygon{
    transition-duration: .2s;
    cursor: pointer;
}

.follow-me_text{
    margin-top: 25px;
    background-color: #33333342;
    padding: 10px;
    border-radius: 10px;
    text-shadow: rgb(66, 58, 40) 0px 0 2px;
}

.route-builder_button{
    text-align: center;
    margin: 0 auto;
    display: block;
    background-image: linear-gradient(90deg, #FAE22F, #FF130E);
    border-radius: 30px;
    padding: 10px;
    color: black;
    width: 15%;
    transition: .2s;
}

.route-builder_button:hover{
    transform: scale(1.15);
}

g > *
{
    cursor: pointer;
}
    g > text {
        pointer-events: none;
    }

    g > use {
        pointer-events: none;
    }

polygon:hover {
    fill: #414141;
    cursor: pointer;
}

path:hover{
    fill: #414141;
    cursor: pointer;
}


#KO > *:hover
{
    fill-opacity: 1;
    fill: rgb(0, 255, 34);
}


.line
{
    margin-left: 20px;
    border: none;
    height: 7px;
    color: #333333; /* old IE */
    background-color: #333; /* Modern Browsers */
    width: 65px;
    opacity: 1;
}

.corrected-button{
    transform: scale(2) translateY(6px);
    margin-left: 15px;
    font-weight: bold;
    transition-property: "transform";
    transition-duration: .2s;
    display: inline-block;
}

.corrected-button:hover{
    transform: scale(3) translateY(6px);
}

.navigation-button
{
    margin-left: 15px;
    font-weight: bold;
    transform: scale(2);
    transition-property: "transform";
    transition-duration: .2s;
    display: inline-block;
}

.municipality-list > li{
    margin-top: 10px;
}

.text-button
{
    font-size: 1.8rem;
    transform: scale(1);
    transition-property: "transform";
    transition-duration: .3s;
}

.text-button:hover
{
    transform: scale(1.3);
}

.navigation-arrow{
    display: inline-block;
    transform: scale(1.5);
}

.navigation-button:hover
{
    transform: scale(3);
}

.trainSchedule{
    background-color: #930E43;
}

.busSchedule{
    background-color: rgb(0, 87, 92);
}

.gradient-background
{
    position: absolute;
    z-index: -0;
    transform: translate(15px, 15px);
    background-image: linear-gradient(to right, #ffc508, #ff1908) 30;
    height: 771px;
    width: inherit
}

.carousel::before
{
    content: "";
    background-color: #00575C;
    position: absolute;
    height: 100%;
    width: 100%;
    transform: translate(15px, 15px);
    z-index: -1;
}

.div-tab {
    width: 512px;
    height: 800px;
    background-color: rgba(255, 230, 0, 0.5);
}

