.div-poi__main {
    margin-top: 5%;
    display: flex;
    font-size: 25px;
    font-weight: bold;
    width: 1850px;
    height: 690px;
    align-items: center;
}

.div-poi {
    align-self: center;
    text-align: center;
    transform: rotate(270deg);
    width: 385px;
    height: 60px;
    border-radius: 15px;
    background-color: #FFE263;
}

.div-poi_content {
    text-align: center;
}

.div-poi_content th {
    border-collapse: separate;
    border: solid;
    border-radius: 15px;
    color: black;
    margin: 3px;
}

.div-poi_content img {
    border: solid;
}

.div-poi_content img {
    border-radius: 15px;
    width: 350px;
    height: 200px;
}
.div-logistic {
    display:flex; 
    margin-top: 2%;
    justify-content: center;    
    flex-wrap:wrap;
    font-size: 24px;
    font-weight: bold;
}
    .div-logistic ul {
        list-style-type: none;
        margin: 25px 0px 0px 0px;
        color: black;
    }
    .div-logistic div {
        margin: 5px;
        border: solid;
    }
    .div-logistic tr{
        display: grid;
        text-align: center;
        justify-items: center;
    }
    .div-logistic img {
        border: solid;
        margin-top: 10%;
        width: 350px;
        height: 150px;
    }

